:root {
  --font-Inter: "Inter", sans-serif;
}

:root {
  --green: hsl(75, 94%, 57%);
  --white: hsl(0, 0%, 100%);
  --grey: hsl(0, 0%, 20%);
  --darkGrey: hsl(0, 0%, 12%);
  --offBlack: hsl(0, 0%, 8%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  max-width: 1440px;
  max-height: 960px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-Inter);
  background-color: var(--offBlack);
}

html,
body {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  color: var(--white);
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 0.75rem;
}

p {
  margin: 0;
  color: var(--green);
  font-size: 0.875rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

span {
  font-size: 0.875rem;
  font-weight: 400;
  color: var(--white);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.wrapper {
  margin: 1.5rem;
}
@media (min-width: 71.875em) {
  .wrapper {
    margin: 0;
  }
}
.wrapper .card {
  max-width: 23.875rem;
  max-height: 37.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 1.5rem;
  padding: 1.5rem;
  background-color: var(--darkGrey);
  border-radius: 0.75rem;
}
@media (min-width: 71.875em) {
  .wrapper .card {
    padding: 2.5rem;
  }
}
.wrapper .card__image img {
  display: block;
  max-width: 100%;
  height: auto;
  border-radius: 50%;
}
.wrapper .card__text {
  text-align: center;
  padding-inline: 0.625rem;
}
@media (min-width: 71.875em) {
  .wrapper .card__text {
    padding-inline: 1.25rem;
  }
}
.wrapper .card__boxes {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 0.875rem;
  width: 100%;
  text-align: center;
}
.wrapper .card__boxes--links {
  width: 100%;
  background-color: var(--grey);
  padding: 0.75rem;
  border-radius: 0.5rem;
  transition: background-color 0.5s ease;
}
.wrapper .card__boxes--links a {
  color: var(--white);
  margin: 0;
  font-size: 1rem;
  font-weight: 600;
}
@media (min-width: 71.875em) {
  .wrapper .card__boxes--links:hover {
    background-color: var(--green);
    cursor: pointer;
  }
}
.wrapper footer {
  margin-top: 0.625rem;
}
.wrapper footer .attribution {
  color: var(--white);
  font-size: 11px;
  text-align: center;
}
.wrapper footer .attribution a {
  color: #3e52a3;
}